export default {
    data(){
        return {
            msg: '',
            arr: arrAll,
		    prov: '',
		    city: '',
		    district: '',
		    cityArr: [],
			districtArr: [],
			data: {},
			name:'',
			phone: '',
			area: '',
			number:'',
			adress:'',
			shade: false,
			warn: ''
        }
    },
    methods: {
		updateCity: function () {
			for (var i in this.arr) {
				var obj = this.arr[i];
				if (obj.name == this.prov) {
					this.cityArr = obj.sub;
					break;
				}
			}
			this.city = this.cityArr[1].name;
		},
		updateDistrict: function () {
			for (var i in this.cityArr) {
				var obj = this.cityArr[i];
				if (obj.name == this.city) {
					this.districtArr = obj.sub;
					break;
				}
			}
			if(this.districtArr && this.districtArr.length > 0 && this.districtArr[1].name) {
				this.district = this.districtArr[1].name;
			} else {
				this.district = '';
			}
		},
		save () {
        	this.$http.get(this.getIp()+'/mobile.php/member/adressadd?name='+this.name+'&phone='+this.phone+'&area='+this.area+'&adress='+this.prov+','+this.city+','+this.district+'&number='+this.number)
        	    .then((res) => {
					console.log(res);
					console.log("");
					// this.$router.go(-1);
        	        // this.data = res.body.data;
        	        // console.log(this.data);
					if(res.body.code === 200)
					{
						// setTimeout(function(){
						this.warn = ''    
						this.shade = true;
						// },1000)
					}else if(res.body.code === 205)  
					{
						this.warn = ''
						this.shade = true;
					}else if(res.body.code === 300)
					{
						this.warn = ''
						this.shade = true;
					}
        	})			
		}

    },
    created()
    {
        this.$http.get(this.getIp()+'/mobile.php/member/adress')
            .then((res) => {
                console.log(res);
				this.data = res.body.data;
				if(this.data == '')
				{
					return;
				}
				this.name = this.data.name;
				this.phone = this.data.phone;
				this.number = this.data.number;
				this.area = this.data.area;
				this.prov = this.data.adress.split(',')[0];
				this.city = this.data.adress.split(',')[1];
				this.district = this.data.adress.split(',')[2];
				// console.log(this.prov);
                // console.log(this.data.adress.split(','));
        })
    },
    components: {
		'v-back':back,
		shade
    },
	beforeMount: function () {
		this.updateCity();
		this.updateDistrict();
	},
	watch: {
		prov: function () {
			this.updateCity();
			this.updateDistrict();
		},
		city: function () {
			this.updateDistrict();
		}
	}
  
}
</script>
<style scoped lang="less" rel="stylesheet">
#address {
    padding-top: 60px;
    .item {
        background-color: #fff;
        position: relative;
        border-top: 1px solid #ddd;
        height: 40px;
        label {
            position: absolute;
            width: 70px;
            left: 0px;
            top: 50%;
            transform: translateY(-50%);
            font-size: 16px;
            color: #666;
            text-align: right;
        }
        input {
            width: 75%;
            margin-left: 80px;
            margin-bottom: 0;
            border: none;
            padding-left: 0;
            font-size: 16px;
        }
    }
    .btn {
        position: fixed;
        right: 4px;
        top: 8px;
        background-color: transparent;
        border: none;
        color: #fff;
        font-weight: 700;
        z-index: 100;
    }

    // 
    #linkage {
        overflow: hidden;
        position: absolute;
        right: 0;
        top: 0;
        z-index: 99;
        width: 74%;
        select {
            float: left;
            margin: 0;
            width: 33.33%;
            padding: 10px 0 0;
            // background-color: red;
        }
    }
}
</style>



